
.contacts-section {
    @media screen and  (min-width: 480px)  {  
      gap: 16px;
      padding-top: 40px;
      padding-bottom: 20px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      max-width: 1200px;
      max-height: 1487px;
      background-color: 
      rgba(132, 161, 120, 1);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center top (320px);

  
    }
     @media screen and  (min-width: 768px)  { 
      max-height: 625px;
      background-position: center top (625px);
      display: flex;
   
      flex-direction: row;
      gap: 16px;
      padding-top: 40px;
      padding-bottom: 40px;
      display: flex;
      flex-wrap: wrap;
      align-items: stretch;
      justify-content: space-around;
      
     }

     @media screen and  (min-width: 1200px)  {  
     
      display: flex;
      flex-direction: row;
      gap: 32px;
      padding-top: 40px;
      padding-bottom: 40px;
      display: flex;
      flex-wrap: wrap;
      align-items: stretch;
      justify-content: space-around;
       
    
      }
  }
  
  
  ul,li{
    text-decoration: none;
    list-style: none;
  }

  .contacts-container{
   @media screen and  (min-width: 480px)  {  
  display: flex;
