.backdrop1,
.backdrop2,
.backdrop3,
.backdrop4,
.backdrop5,
.backdrop7,
.backdrop8 {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 250;
    opacity: 1;
    // overflow-y: scroll;
    overflow-x: hidden;
    background-color: rgba(0, 0, 0, 0.2);
    // transition: opacity $transition, visibility $transition;
}

.backdrop1.is-hidden1,
.backdrop2.is-hidden2,
.backdrop3.is-hidden3,
.backdrop4.is-hidden4,
.backdrop5.is-hidden5,
.backdrop7.is-hidden7,
.backdrop8.is-hidden8 {
    opacity: 0;
    pointer-events: none;
    // visibility: hidden;
    
}