 .reviews {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

 // mobail
    background-image: url(../images/Backgroud_mobile885x1242.jpg);

    @media screen and (min-device-pixel-ratio: 2),
     screen and (min-resolution: 192dpi),
     screen and (min-resolution: 2dppx) { 
     background-image: url(../images/Backgroud_mobile1770x2484@2x.jpg);
          
 }
// tablet
    @media screen and (min-width: 768px) {
     background-image: url(../images/Background_tablet963x1017.jpg);
    }
    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/Background_tablet1926x2034@2x.jpg);
    }
    
     //desctop
    @media screen and (min-width: 1200px) {
        background-image: url(../images/Backgroud_desktop1300x1350.jpg);
     }
    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/Backgroud_desktop2600x2700@2x.jpg);
    }
        
        
        @media screen and (max-width: 767px) {
             padding-top: 80px;
        }
        @media screen and (min-width: 768px) and  (max-width: 1199px) {
             padding-top: 100px;
        }
        @media screen and (min-width: 1200px) {
            padding-top: 140px;
        }
}

.swiper-slide {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
}
.reviews__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 85px;
    height: 85px;
    border-radius: 50%;
    background-color: var(--extrabackgroundColor);

    @media screen and (min-width: 768px) and  (max-width: 1199px) {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 84px;
        height: 84px;
        border-radius: 50%;
        background-color: var(--extrabackgroundColor);
    }
}
.reviews__smail {
    border-radius: 50%;
}
.reviews__one {
    position: relative;

    margin-bottom: 32px;
    margin-top: 32px;
    width: 280px;
    height: 256px;
    background-color: var(--backgroundColor);
    border-radius: 10px;

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        display: flex;
        text-align: center;
        justify-content: center;

        width: 705px;   
        height: 138px;
    }
    @media screen and (min-width: 1200px) {
        display: flex;
        text-align: center;
        justify-content: center;

        width: 968px;   
        height: 138px;
    }
}
.reviews__text {
    
    @media screen and (max-width: 767px) {
        text-align: center;
        position: absolute;
        padding: 35px 40px;
    }
    
    

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 255%;

    color: var(--extrabackgroundColor);
    
    @media screen and (min-width: 768px) and  (max-width: 1199px) {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 186%;
        
        
        padding-left: 80px;
        padding-right: 80px;
        padding-top: 28px;
        padding-bottom: 29px;

        text-align: center;
        letter-spacing: 0.04em;
    }
    @media screen and (min-width: 1200px) {
        padding: 43px 109px;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 186%;
/* or 30px */

        text-align: center;
        letter-spacing: 0.04em;
        
    }
    
}
.reviews__title {
    margin-bottom: 35px;
    

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 186%;


    text-align: center;

    color: var(--primaryColor);
}
.visually-hidden {
    white-space: nowrap;
    width: 1px;
    height: 1px;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    border: 0;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
}
.swiper .swiper-pagination-bullet-active {
    background-color: var(--backgroundColor);
    width: 10px;
    height: 10px;
}

